import React, { Component } from "react";
import { connect } from 'react-redux';
import { actions } from '../Redux/actions/Action';
import { NavLink, useRouteMatch } from "react-router-dom";
import IconCard from './helpComponents/IconCard'
import { openfileDialog } from '../BL/StyleCard'
import LogoProfile from './LogoProfile'
import UploadFile from './helpComponents/UploadFile'
import $ from 'jquery'
import '../Css/CardDesktop.css'
import '../Css/CardDesktop.css'
import '../custom.css'
import LogoProfileDesktop from './LogoProfileDesktop'

class CardDesktop extends Component {
    constructor(props) {
        super(props);
        this.state = {
            openTextAboutMe: false,
            agentStyle: {},
            // match:useRouteMatch(),
            numSocialMedia: 6,
            fellowName:"",
            fellowEmail:"",
            fellowMobile:"",
            massegeBody: "",
            massege:""
        };
        this.setFellowDetails=this.setFellowDetails.bind(this);
    }

    getIconElement = (icon, title, i) => {
        return <div className="" key={i}>
            <IconCard icon={icon} title={title}
                titleColor={this.props.vCardStyle.socialMediaTitleIconColorDesktop}
                background={this.props.vCardStyle.socialMediaIconColorDesktop}
                iconShap={this.props.vCardStyle.socialMediaIconShape}
            ></IconCard>
        </div>
    }

    getIconElementsBySocialMediaType = (num, rows) => {
        for (var i = 0; i < num; i++)
            rows.push(this.getIconElement(['fas', 'plus'], "Icon", i));
    }

    setFellowDetails(e) {
          switch(e.target.id){
              case "fellowName":{
                  this.setState({fellowName:e.target.value});break;
                }
              case "fellowEmail":{
                  this.setState({fellowEmail:e.target.value});break;
                }
              case "fellowMobile":{
                  this.setState({fellowMobile:e.target.value});break;
                }
              case "massegeBody":{
                    this.setState({massegeBody:e.target.value});break;
                }  
          }

      }

    getFellowDetails=()=>
    {
        debugger;
        var massege = `<h2 style="text-align:left; backgroun-color:red">שלום וברכה </h2>
        <h3 style="text-align:left; backgroun-color:red">form ${this.state.fellowName}</h3>
        <h3 style="text-align:left; backgroun-color:red">form ${this.state.fellowEmail}</h3>
        <h3 style="text-align:left; backgroun-color:red">form ${this.state.fellowMobile}</h3>
        <h3 style="text-align:left; backgroun-color:red">form ${this.state.massegeBody}</h3>`

        this.setState({ fellowName: "", fellowEmail: "", fellowMobile: "", massegeBody: "" });
        this.props.sendEmail(massege)
    }


    render() {

        return (
          this.props.card? <div className=" m-0 p-0 cardDesktop" id="scrollbarDesktop">
                <UploadFile
                   id={"fileLoaderCover"}
                    changeCoverStyle={this.props.changeCoverStyle}
                    idElementChange={"#agentDesktop"}
                ></UploadFile>

                {/* header */}
                <div className="row m-0 p-0 h-25 d-flex align-items-stretc" id="agentDesktop"
                    style={{ background: this.props.vCardStyle.coverImg, backgroundSize: "cover" }}
                   >
                    <LogoProfileDesktop></LogoProfileDesktop>
                </div>

                {/* body */}
             
                <div className="row mt-4 h-75 d-flex justify-content-center">
                    <div className="col d-flex justify-content-center">
                        <div className="row w-75  bodyDesktop"
                            style={{ background: this.props.card.vCardStyle.bodyColorDesktop }}
                        >
                                <div className="row w-100 m-0 mt-5 p-0 " >
                                    <div className="col-12 text-center m-0  p-0" id="fullNameAgentDesktop"
                                        hidden={this.props.card.vCardStyle.fullNameShow}
                                        style={{ color: this.props.card.vCardStyle.fullNametitleColorDesktop }}
                                    > {this.props.card.agentDetails.fullName !== "" ? this.props.card.agentDetails.fullName : ""}</div>

                                    <div className="col-12 text-center m-1 p-0" id="ruleDesktop"  
                                        style={{ color: this.props.card.vCardStyle.ruletitleColorDesktop}}
                                    >                                    
                                     {this.props.card.agentDetails.rule !== "" ? this.props.card.agentDetails.rule : ""}
                                    </div>
                                </div>

                                <div className="row container mt-2  ml-5 p-0 d-flex justify-content-start ">
                                    <div className="col container">
                                        <label className="row about">About Us</label>
                                        <p className=" rounded textMax mb-2"id="">
                                           {this.props.card.agentDetails.personalMassegeConnect !== "" ? this.props.card.agentDetails.personalMassegeConnect
                                                : `Everage Agile Frameworks To Provide A Robust Synopsis For
                                                High Level Overviews. Iterative Approaches To Corporate Strategy
                                                Foster Collaborative Thinking To Further `}
                                        </p>
                                        <label className="row about">About Me</label>
                                        <div className=" rounded textMax">
                                         {this.props.card.agentDetails.aboutConnect !== "" ? this.props.card.agentDetails.aboutConnect
                                          : `Bring to the table win-win survival strategies to ensure proactive domination. At the end of the day, going forward, a new normal that has evolved from generation X is on the runway heading towards a streamlined cloud solution. User generated content in real-time will have multiple touchpoints for offshoring .`}
                                        </div>
                                         
                                        <div className="row container" id="socialMedia" >
                                            {
                                                this.props.card.socialMedias.map((socialMedia, i) => {
                                                    if (socialMedia.url && socialMedia.url !== "" && socialMedia.name !== "Whatsapp") {
                                                        return <a  className="col-1" key={i} href={this.props.card.socialMedias[i].url}>{this.getIconElement(this.props.socialMediaIcons[socialMedia.name], socialMedia.name, i)}</a>
                                                    }
                                                    return ""
                                                })
                                            }
                                           
                                        </div>
                                    </div>

                                    {/* <div className="row bg-info"> gf6fdytdtrg</div> */}
                                </div>

                                <div className="mt-3 footerDesktop h-80 w-100 d-flex justify-content-center align-items-center"
                                    style={{ background: this.props.card.vCardStyle.bodyColor }} >
                                    <div className="col container">
                                        <div className=" row mt-4 d-flex justify-content-center">
                                            <h1 style={{ color: this.props.card.vCardStyle.fullNametitleColor }}>
                                                Tell us about yourself</h1>
                                        </div>
                                        <div className="row mt-4 d-flex d-flex justify-content-between" style={{ width: "87%", margin: "auto" }}>
                                            <input id="fellowName" className="rounded border-0" style={{ width: "30%" }} placeholder="full name"
                                              value={this.state.fellowName} onChange={this.setFellowDetails} ></input>
                                            <input id="fellowEmail" className="rounded border-0" style={{ width: "30%" }} placeholder="e mail"
                                               value={this.state.fellowEmail} onChange={this.setFellowDetails} ></input>
                                            <input  id="fellowMobile" className="rounded border-0" style={{ width: "30%" }} placeholder="mobile"
                                               value={this.state.fellowMobile} onChange={this.setFellowDetails}></input>
                                        </div>

                                        <div className="row  mt-4 d-flex justify-content-around">
                                           <textarea id="massegeBody" className="rounded" style={{width:"85%"}}  value={this.state.massegeBody} onChange={this.setFellowDetails}></textarea>
                                        </div>
                                        <div className="row  
                                        rounded d-flex justify-content-around mt-2">
                                            <div className="d-flex justify-content-center rounded mb-2" style={{
                                                width: "85%",color:"#FFFF",background:"#3A405E" }} onClick={this.getFellowDetails}>send</div>
                                        </div>
                                    </div>
                                </div>
                        </div>
                    </div>
                </div>
            </div >:""
        );
    }
}



const mapStateToProps = (state) => {
    return {
        // agentDetails: state.agentDetails.currentAgentForCard,
        card: state.card.card,
        maxsocialMedia: state.card.maxsocialMedia,
        // socialMedias: state.card.currentCard.socialMedias,
        vCardStyle: state.card.card?state.card.card.vCardStyle:null,
        socialMediaIcons: state.card.socialMediaIcons
    };
}

const mapDispatchToProps = (dispatch) => ({

    sendEmail: (content) => dispatch(actions.sendEmail(content)),

    setAboutConnect: (content) => dispatch(actions.setAboutConnect(content)),

    setPersonalmassegeConnect: (content) => dispatch(actions.setPersonalmassegeConnect(content)),

    setAgentDetailsToggle: () => dispatch(actions.setAgentDetailsToggle("card")),

    setSocialMediaToggle: () => dispatch(actions.setSocialMediaToggle("card")),

    setvCardStyleToggle: () => dispatch(actions.setvCardStyleToggle("card")),
})
export default connect(mapStateToProps, mapDispatchToProps)(CardDesktop);
