import { actions } from '../actions/Action';

export const setCard = ({ dispatch, getState }) => next => action => {
    var url = window.location;
        var splidUrl = url.pathname.split('/');
        debugger
    var uid = splidUrl[2];
    var cardId = splidUrl[3];
        // 
        if (cardId) {
                if (action.type === 'CARD_INIT') {
                    return fetch('https://knowme.page/api/digitalcard/'+ uid+"/"+ cardId + '?jwt?=123'
                        ).then((res) => res.json())
                                .then((resJson) => {
                                        dispatch(actions.setCard(resJson));
                                })
                                .catch((err) => {
                                        console.log(err)
                                })
                }
        }
    return next(action);

};

