import React, { Component } from "react";
import { connect } from 'react-redux';
import { actions } from '../Redux/actions/Action';
import { NavLink, useRouteMatch } from "react-router-dom";
import IconCard from './helpComponents/IconCard'
import { openfileDialog } from '../BL/StyleCard'
import CardDesktop from './CardDesktop'
import LogoProfile from './LogoProfile'
import UploadFile from './helpComponents/UploadFile'
import $ from 'jquery'
import '../Css/LogoProfile.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import '../custom.css'




class Card extends Component {
    constructor(props) {
        super(props);
        this.state = {
            openTextAboutMe: false,
            openTextAboutUs: false,
            titleAbout: "",
            agentStyle: {},
            // match:useRouteMatch(),
            numSocialMedia: 6
        };
    }


    getIconElement = (icon, title, i) => {
        return <IconCard icon={icon} title={title}
            titleColor={this.props.card.vCardStyle.socialMediaTitleIconColor}
            background={this.props.card.vCardStyle.socialMediaIconColor}
            iconShap={this.props.card.vCardStyle.socialMediaIconShape}
        ></IconCard>

    }
    
   


    render() {

        return (
            this.props.card?<div className="row " >
                    <div className="col container">

                        {/* header */}

                        <div className="row sm-12  mb-0 p-0 d-flex align-items-stretc agentPic " id="agentPic"
                            style={{ background: this.props.card.vCardStyle.coverImg, backgroundSize: "cover !important" }}>
                            <LogoProfile></LogoProfile>

                        </div>
                        {/* / ;" */}
                        {/* body component */}
                        <div className=" row sm-12  d-flex align-items-stretc mt-0 p-0 agentDetails" id="agentDetails"
                            style={{ background: this.props.card.vCardStyle.bodyColor }}>
                            <div className="col sm-12 container"  >
                                {/* <NavLink to={`${this.props.match.url}/agentDetails`}> */}
                                <div className="row w-100 m-0 mt-5 p-0" onClick={this.props.setAgentDetailsToggle}>

                                    <label className="col-sm-12 text-center m-0  p-0" id="fullNameAgent"
                                        hidden={this.props.card.vCardStyle.fullNameShow}
                                        style={{ color: this.props.card.vCardStyle.fullNametitleColor }}>
                                        {this.props.card.agentDetails.fullName !== "" ? this.props.card.agentDetails.fullName : "Name"}
                                    </label>

                                    <label className="col-12 text-center m-1 p-0" id="rule"
                                        hidden={this.props.card.vCardStyle.ruleShow}
                                        style={{ color: this.props.card.vCardStyle.ruletitleColor }}
                                    >
                                        {this.props.card.agentDetails.rule !== "" ? this.props.card.agentDetails.rule : "Role"}
                                    </label>
                                </div>
                                {/* </NavLink> */}




                                {/* card socialmedia component */}

                                {/* <NavLink to={`${this.props.match.url}/socialMedia`}> */}

                                <div className="row d-flex justify-content-center" id="socialMedia" >
                                    <a className="col-4 text-nowrap d-flex justify-content-center mt-3" href={`tel:+${this.props.card.agentDetails.mobile}`}> {this.getIconElement(['fas', 'phone-alt'], "Call Me", "Call Me")}</a>
                                    <a className="col-4 text-nowrap d-flex justify-content-center mt-3" href={`mailto:${this.props.card.agentDetails.personalEmail}`}> {this.getIconElement(['fas', 'paper-plane'], "Mail", "Mail")}</a>

                                    {
                                        this.props.card.socialMedias.map((socialMedia, i) => {
                                            if (socialMedia.url && socialMedia.url !== "") {
                                                if (socialMedia.name == "Whatsapp") {
                                                    return <a className="col-4 text-nowrap d-flex justify-content-center mt-3" key={i} href={`whatsapp:contact=${socialMedia.url}`} >{this.getIconElement(this.props.socialMediaIcons[socialMedia.name], socialMedia.name, i)}</a>;
                                                }
                                                return <a className="col-4 text-nowrap d-flex justify-content-center mt-3" key={i} href={this.props.card.socialMedias[i].url}>{this.getIconElement(this.props.socialMediaIcons[socialMedia.name], socialMedia.name, i)}</a>;
                                            }
                                        })
                                    }

                                </div>
                                {/* </NavLink> */}
                                {/* about component */}
                                <div className="row-6 mt-2 ml-4" id="about">{this.state.titleAbout}</div>

                                <div className="row w-100 m-0 p-0 d-flex justify-content-center " onClick={this.props.setAgentDetailsToggle}>
                                    <div className="col container  text left " id="aboutMe">
                                        <div className="row  border-bottom"
                                            hidden={this.state.openTextAboutUs || this.state.openTextAboutMe}>
                                            <label className="col-10" >About Us</label>
                                            <label className="col-2" onClick={(e) => { this.setState({ openTextAboutUs: !this.state.openTextAboutUs, titleAbout: "About Us" }) }}>
                                                <svg xmlns="http://www.w3.org/2000/svg" width="15.529" height="9" viewBox="0 0 15.529 9">
                                                    <g fill="#fff" stroke="#fefefe" transform="translate(0.564 0.5)">
                                                        <g transform="translate(0 0)"><path d="M.176,117.517a.589.589,0,0,1,.848,0L7.2,123.851l6.176-6.334a.589.589,0,0,1,.848,0,.626.626,0,0,1,0,.87l-6.6,6.769a.589.589,0,0,1-.848,0l-6.6-6.769A.626.626,0,0,1,.176,117.517Z" transform="translate(0 -117.336)" /></g></g></svg>

                                            </label>
                                        </div>

                                        {this.state.openTextAboutUs || this.state.openTextAboutMe ?
                                            <div className="row rounded ml-2" id="abuotMetext"
                                                onChange={(e) => this.props.setPersonalmassegeConnect(e.target.value)}
                                            >
                                                <FontAwesomeIcon icon={["fas", 'times']} className="row iconClose mr-4" onClick={(e) => {
                                                    this.state.openTextAboutMe ? this.setState({ openTextAboutMe: !this.state.openTextAboutMe, titleAbout: "" }) : this.setState({ openTextAboutUs: !this.state.openTextAboutUs, titleAbout: "" })
                                                }
                                                }></FontAwesomeIcon>
                                                {this.state.openTextAboutUs ? this.props.card.agentDetails.personalMassegeConnect : this.state.openTextAboutMe ? this.props.card.agentDetails.aboutConnect : ""}
                                                {this.props.card.agentDetails.personalMassegeConnect !== "" ? this.props.card.agentDetails.personalMassegeConnect
                                                    : `Everage Agile Frameworks To Provide A Robust Synopsis For
                                                    High Level Overviews. Iterative Approaches To Corporate Strategy
                                                    Foster Collaborative Thinking To Further`}
                                            </div> : ""}
                                    </div>
                                </div>
                                <div className="row w-100 m-0 p-0 d-flex justify-content-center" onClick={this.props.setAgentDetailsToggle}>
                                    <div className="col container   text-left p-3" id="aboutMe" hidden={this.state.openTextAboutUs}>
                                        <div className="row border-bottom"
                                            hidden={this.state.openTextAboutMe || this.state.openTextAboutUs}>

                                            <label className="col-10">About Me</label>
                                            <label className="col-2" onClick={(e) => { this.setState({ openTextAboutUs: this.state.openTextAboutUs, openTextAboutMe: !this.state.openTextAboutMe, titleAbout: "About Me" }) }}>
                                                <svg xmlns="http://www.w3.org/2000/svg" width="15.529" height="9" viewBox="0 0 15.529 9">
                                                    <g fill="#fff" stroke="#fefefe" transform="translate(0.564 0.5)">
                                                        <g transform="translate(0 0)"><path d="M.176,117.517a.589.589,0,0,1,.848,0L7.2,123.851l6.176-6.334a.589.589,0,0,1,.848,0,.626.626,0,0,1,0,.87l-6.6,6.769a.589.589,0,0,1-.848,0l-6.6-6.769A.626.626,0,0,1,.176,117.517Z" transform="translate(0 -117.336)" /></g></g></svg>
                                            </label>

                                        </div>
                                        {/* {this.state.openTextAboutMe ?
                                            <div className="row rounded ml-2" id="abuotMetext" 
                                                onChange={(e) => this.props.setAboutConnect(e.target.value)}
                                               > <FontAwesomeIcon icon={["fas",'times']} className="row iconClose" onClick={(e) => { 
                                                this.setState({ openTextAboutMe: !this.state.openTextAboutMe}) }
                                                }></FontAwesomeIcon>
                                           
                                                   {this.props.card.agentDetails.aboutConnect !== "" ? this.props.card.agentDetails.aboutConnect
                                               : `Bring to the table win-win survival strategies to ensure proactive domination. At the end of the day, going forward, a new normal that has evolved from generation X is on the runway heading towards a streamlined cloud solution. User generated content in real-time will have multiple touchpoints for offshoring.`}
                                            </div> :""} */}

                                    </div>
                                </div>



                            </div>
                        </div>

                    </div>

                </div>:""
        );
    }
}



const mapStateToProps = (state) => {
    return {
        // agentDetails: state.card.agentDetails,
        card: state.card.card,
        maxsocialMedia: state.card.maxsocialMedia,
        socialMediaIcons: state.card.card?state.card.socialMediaIcons:null,
        screenSize: state.card.screenSize
    };
}

const mapDispatchToProps = (dispatch) => ({
    changeFullName: (name) => dispatch(actions.setFullname(name)),

    changeRule: (rule) => dispatch(actions.setRule(rule)),

    changeMobileTitle: (mobileTitle) => dispatch(actions.setMobiletitle(mobileTitle)),

    changeCoverStyle: (style) => dispatch(actions.setCovercardStyle(style)),

    setAboutConnect: (content) => dispatch(actions.setAboutConnect(content)),

    setPersonalmassegeConnect: (content) => dispatch(actions.setPersonalmassegeConnect(content)),

    setAgentDetailsToggle: () => dispatch(actions.setAgentDetailsToggle("card")),

    setSocialMediaToggle: () => dispatch(actions.setSocialMediaToggle("card")),

    setvCardStyleToggle: () => dispatch(actions.setvCardStyleToggle("card")),
})

export default connect(mapStateToProps, mapDispatchToProps)(Card);
