import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux';
import '../../Css/helpComponentCss/IconCard.css'
import { useState, useEffect } from 'react';

import $ from 'jquery'
const mapStateToProps = (state) => {
    return {
        screenSize: state.card.screenSize
    };
}


export default connect(mapStateToProps)(function IconCard(props) {

    const {title, background, iconShap, icon, titleColor, screenSize } = props;

    return (
        <div className="container-fluid d-flex justify-content-center"> 
            {/* iconContactDesktop */}
            <div className="row iconContact d-flex align-items-center justify-content-center"
                style={{
                    borderColor: background,
                    borderRadius: iconShap,
                    borderWidth: "medium"
                }}
                title={title ? title : "Add SocialMedia"}
            >
                <FontAwesomeIcon icon={icon} color={background} />
            </div>
            {/* <div className="row"> hi</div> */}
            {/* <div className=" row mt-1 d-flex justify-content-center ">
                <label className="  addiconContact  d-flex justify-content-center d-md-none" style={{ color: titleColor }} hidden={screenSize === 0 ? false : true}>{title ? title : "Icon"}</label>
            </div> */}
        </div>
    );

});


