import { createStore, combineReducers, applyMiddleware } from 'redux';
import { sendEmail, setCard} from '../MiddleWares/CardMiddleWare'
import card from '../reducers/CardStore'


const reducer = combineReducers({card});

const store = createStore(reducer, applyMiddleware(setCard,sendEmail));
window.store = store;
export default store;
store.dispatch({ type: "CARD_INIT" });